import { Component } from "@angular/core";
import { Todo } from "./models/Todo";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"]
})
export class AppComponent {
  Item: Todo = { name: "", isCompleted: false };
  ItemList: Todo[] = [];

  AddItem() {
    this.ItemList.push({
      name: this.Item.name,
      isCompleted: this.Item.isCompleted
    });

    this.Item.name = "";
  }

  Complete(todo: Todo) {
    todo = this.ItemList.find(x => x.name == todo.name);
    todo.isCompleted = todo.isCompleted === false ? true : false;
  }

  Delete(todo: Todo) {
    let index = this.ItemList.indexOf(todo);
    this.ItemList.splice(index, 1);
  }
}



